import React from 'react';
import Helmet from 'react-helmet';
import { Fade } from 'react-reveal';

import Header from '../../components/Header/Header';
import Container from '../../components/Container/Container';
import Footer from '../../components/Footer/Footer';
import config from '../../../config/SiteConfig';
import styles from './coding.module.scss';


const Coding = (props) => {
  const { projects } = props.pathContext;
  return (
    <div className="container coding-container">
      <Helmet title={`Coding | ${config.siteTitle}`} />
      <Header>Coding</Header>
      <Container text>
        {projects.map((project, idx) => (
          <div className={styles.project} key={idx}>
            <Fade>
              <h3 className={styles.title}>{project.title}</h3>
              <p className={styles.description}>
                {project.description}
              </p>
              <p>
                <a {...project.anchor} className={styles.anchor}>
                See more
                </a>
              </p>
            </Fade>
          </div>
        ))}
      </Container>
      <Footer />
    </div>
  );
}

export default Coding;



